import Vue from 'vue'
import Router from 'vue-router'
import Markdown from '@/view/markdown'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/markdown/:path(.+)',
      name: 'markdown',
      component: Markdown
    }
  ]
})



